"use client";

import "./globals.css";
import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();

  const [showSidebar, setShowSidebar] = useState(false);
  const [checkingAuth, setCheckingAuth] = useState(true); // ✅ prevents sidebar flicker

  useEffect(() => {
    const checkAuth = async () => {
      const {
        data: { user },
      } = await supabase.auth.getUser();

      // ✅ Sidebar only for logged-in users AND only on these routes
      if (
        user &&
        (pathname === "/library" || pathname === "/workspace")
      ) {
        setShowSidebar(true);
      } else {
        setShowSidebar(false);
      }

      setCheckingAuth(false); // ✅ auth check finished
    };

    checkAuth();
  }, [pathname]);

  // ✅ Prevent UI flicker while auth is being checked
  if (checkingAuth) {
    return (
      <html lang="en">
        <body className="min-h-screen bg-gray-100"></body>
      </html>
    );
  }

  return (
    <html lang="en">
      <body className="flex min-h-screen bg-gray-100">
        {/* ✅ Sidebar only when allowed */}
        {showSidebar && (
          <div className="w-64 bg-white shadow-lg p-6 flex flex-col">
            <h1 className="text-2xl font-bold mb-6">Dashboard</h1>

            <nav className="flex flex-col gap-4">
              <a
                href="/library"
                className="text-lg hover:text-blue-600"
              >
                Content Library
              </a>

              <a
                href="/workspace"
                className="text-lg hover:text-blue-600"
              >
                Content Workspace
              </a>
            </nav>
          </div>
        )}

        {/* ✅ Main Page Content */}
        <main className="flex-1 p-10">{children}</main>
      </body>
    </html>
  );
}
